export default function (num) {
  const n = Number(num);
  /*
  toFixed(0)
  replace
  
  
  (
  1.,
  2.,
  3.,
  4.,
  5.[ES(2018)9]
  )
  */
  return `$${n.toFixed().replace(/./g, (match, offset, str) => {
    const currency = (offset && match !== '.' && ((str.length - offset) % 3 === 0) ? `,${match}` : match);
    return currency;
  })}`;
}
